// src/app/secrets/page.js

'use client';

import { useSession } from 'next-auth/react';
import { useRouter } from 'next/navigation';
import { Loader2 } from 'lucide-react';
import Navigation from '../components/Navigation';
import K8sSecretManager from '../components/K8sSecretManager';

export default function SecretsPage() {
  const { data: session, status } = useSession();
  const router = useRouter();

  // Redirect ke signin jika belum login
  if (status === 'loading') {
    return (
      <div className="min-h-screen bg-neutral-950 flex items-center justify-center">
        <Loader2 size={40} className="animate-spin text-purple-500" />
      </div>
    );
  }


  // Handler untuk navigation (redirect ke halaman lain)
  const handlePageChange = (page) => {
    if (page === 'jenkins') {
      router.push('/');
    } else if (page === 'k8s-secret') {
      router.push('/secrets');
    }
  };

  return (
    <div className="min-h-screen bg-neutral-950">
      <Navigation activePage="k8s-secret" onPageChange={handlePageChange} />
      <main>
        <K8sSecretManager />
      </main>
    </div>
  );
}